import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Icon } from 'antd';
import { useHistory, NavLink } from 'react-router-dom'

import './index.styl';
@inject("store")
@observer
class Header extends Component<Props, object> {
  menus: any[] = [
    {
      id: '1',
      name: '',
      route: '/'
    },
    {
      id: '2',
      name: '',
      route: '/betRecords'
    },
    {
      id: '3',
      name: '',
      route: '/openIssueHistory'
    },
    {
      id: '4',
      name: '',
      route: '/playMethodRule'
    }
  ];
  activeIndex: string = '/';
  content: string[] = [];
  imgSrc: string = '';
  constructor(props: Props) {
    super(props);
  }
  render() {
    return (
      <section className="header-view flex ai-c">
        <div className="wrap">
          <div className="icon-logo"></div>
          <div className="inline-item">
            <span className="username t-yellow f-14">{ this.props.store.user.name }</span>
          </div>
          <div className="inline-item bg-icon-money">
            <Icon className="m-r-10" type="pay-circle" />{ this.props.store.user.balance }
          </div>
          <div className="navs flt-r">
            { this.getNavList() }
          </div>
        </div>
      </section>
    )
  }

  public getNavList() {
    return this.menus.map((nav) => {
      return (
        <NavLink 
          className="nav-item"
          key={nav.id}
          to={nav.route}
        >
          {nav.name}
        </NavLink>
      )
    })
  }
}

export default Header;

